import request from '@/utils/request';
var api = {
  GetfaultRecord: '/api/dms/faultRecord/get',
  GetById: '/api/dms/faultRecord/getById',
  GetFaultReason: '/api/dms/faultReason/get',
  Handle: '/api/dms/faultRecord/handle',
  SendMessage: '/api/dms/faultRecord/SendMessage',
  SetIsSendMsg: '/api/dms/faultRecord/SetIsSendMsg'
}; // 获取故障记录

export function GetfaultRecord(obj) {
  return request({
    url: api.GetfaultRecord,
    methods: 'get',
    params: obj
  });
} // 获取故障详情

export function GetById(id) {
  return request({
    url: api.GetById,
    methods: 'get',
    params: {
      id: id
    }
  });
} // 获取原因列表

export function GetFaultReason(obj) {
  return request({
    url: api.GetFaultReason,
    method: 'get',
    params: obj
  });
} // 处理故障记录

export function Handle(obj) {
  return request({
    url: api.Handle,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 发送故障记录报警短信 

export function SendMessage(obj) {
  return request({
    url: api.SendMessage,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 设置记录是否报警 SetIsSendMsg

export function SetIsSendMsg(obj) {
  return request({
    url: api.SetIsSendMsg,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}