import request from '@/utils/request';
var api = {
  getList: '/api/dms/faultScheme/get',
  createItem: '/api/dms/faultScheme/Create',
  updateItem: '/api/dms/faultScheme/update',
  deleteItem: '/api/dms/faultScheme/delete',
  getphenomenonById: '/api/dms/faultSymptom/get',
  phenomenonDetailById: '/api/dms/faultSymptom/getById',
  getReasonList: '/api/dms/faultReason/get',
  updatePhenomenon: '/api/dms/faultSymptom/update',
  deletePhenomenon: '/api/dms/faultSymptom/delete',
  createPhenomenon: '/api/dms/faultSymptom/create',
  createReason: '/api/dms/faultReason/create',
  deleteReason: '/api/dms/faultReason/delete',
  updateReason: '/api/dms/faultReason/update'
}; // 获取方案列表

export function GetList(obj) {
  return request({
    url: api.getList,
    method: 'get',
    params: obj
  });
} // 创建方案

export function createItem(obj) {
  return request({
    url: api.createItem,
    method: 'post',
    data: obj
  });
} // 更新方案

export function updateItem(obj) {
  return request({
    url: api.updateItem,
    method: 'post',
    data: obj
  });
} // 删除方案

export function Delete(id) {
  return request({
    url: api.deleteItem,
    method: 'post',
    data: {
      id: id
    }
  });
} // 获取现象列表

export function GetphenomenonById(obj) {
  return request({
    url: api.getphenomenonById,
    method: 'get',
    params: obj
  });
} // 获取现象详情

export function PhenomenonDetailById(id) {
  return request({
    url: api.phenomenonDetailById,
    method: 'get',
    params: {
      id: id
    }
  });
} // 新增现象

export function CreatePhenomenon(obj) {
  return request({
    url: api.createPhenomenon,
    method: 'post',
    data: obj
  });
} // 更新现象

export function UpdatePhenomenon(obj) {
  return request({
    url: api.updatePhenomenon,
    method: 'post',
    data: obj
  });
} // 删除现象

export function DeletePhenomenon(id) {
  return request({
    url: api.deletePhenomenon,
    method: 'post',
    data: {
      id: id
    }
  });
} // 获取原因列表

export function GetReasonList(obj) {
  return request({
    url: api.getReasonList,
    method: 'get',
    params: obj
  });
} // 创建原因

export function CreateReason(obj) {
  return request({
    url: api.createReason,
    method: 'post',
    data: obj
  });
} // 更新原因

export function UpdateReason(obj) {
  return request({
    url: api.updateReason,
    method: 'post',
    data: obj
  });
} // 删除原因

export function DeleteReason(id) {
  return request({
    url: api.deleteReason,
    method: 'post',
    data: {
      id: id
    }
  });
}