import { GetById, GetFaultReason, Handle } from '@/api/faultManage/analysis';
import CreateFault from "../analysis/detail/editContent/fault/createFault";
export default {
  name: 'FaultDetails',
  components: {
    CreateFault: CreateFault
  },
  data: function data() {
    return {
      dialogVisible: false,
      faultDate: {
        device: {
          code: '',
          id: '',
          name: ''
        },
        deviceType: {
          code: '',
          id: '',
          name: ''
        },
        employees: [{
          id: '',
          mobile: '',
          name: '',
          nickName: ''
        }],
        id: '',
        isHandled: '',
        symptoms: [{
          name: '',
          duration: ''
        }],
        reasons: [{
          name: '',
          weight: ''
        }],
        logs: [{
          content: '',
          createTime: '',
          time: ''
        }]
      },
      deviceId: '',
      recordId: '',
      realReasonId: 0,
      reasonsArr: [{
        name: 1
      }, {
        name: 13
      }, {
        name: 14
      }],
      createFaultView: false
    };
  },
  methods: {
    init: function init(id) {
      var _this = this;

      this.recordId = id;
      this.dialogVisible = true;
      this.$nextTick(function () {
        GetById(id).then(function (res) {
          if (res.data.logs) {
            res.data.logs.map(function (item) {
              item.createTime = _this.$moment.utc(item.createTime).local().format('YYYY-MM-DD HH:mm:ss');
            });
          } else {
            return;
          }

          _this.deviceId = res.data.device.id;
          var data = res.data;

          _this.$set(_this, 'faultDate', data);
        });
      });
    },
    handleErr: function handleErr() {
      var _this2 = this;

      GetFaultReason({
        pageIndex: 1,
        pageSize: 200,
        deviceId: this.deviceId,
        faultRecordId: this.recordId
      }).then(function (res) {
        _this2.reasonsArr = res.data.items;
      });
    },
    handleCommand: function handleCommand(command) {
      var _this3 = this;

      if (command.type === 1) {
        this.createFaultView = true;
        this.$nextTick(function () {
          _this3.$refs.createFault.init();
        });
        this.handleErr();
      } else {
        this.realReasonId = command.data.id;
        Handle({
          recordId: this.recordId,
          realReasonId: this.realReasonId
        }).then(function () {
          _this3.dialogVisible = false;

          _this3.$parent.init();
        });
      }
    },
    hiddleClose: function hiddleClose() {
      this.$emit('reset');
    }
  }
};